/* 产品-网络模块 */
import request from "../utils/request";
/* vpc */
export function getVpc (params) {
  return request.get(`/vpc`, { params });
}
export function addVpc (params) {
  return request.post(`/vpc`, params);
}
export function updateVpc (params) {
  return request.put(`/vpc/${params.id}`, params);
}
export function deleteVpc (id) {
  return request.delete(`/vpc/${id}`);
}

/* 安全组 */
export function getGroup (params) {
  return request.get(`/security_group`, { params });
}
export function getGroupDetail (id) {
  return request.get(`/security_group/${id}`);
}
export function addAndUpdateGroup (type, params) {
  if (type === 'add') {
    return request.post(`/security_group`, params);
  } else if (type === 'update') {
    return request.put(`/security_group/${params.id}`, params);
  }
}
export function deleteGroup (id) {
  return request.delete(`/security_group/${id}`);
}

/* 安全组规则 */
export function getGroupRules (params) {
  return request.get(`/security_group/${params.id}/rule`, { params });
}
export function addAndUpdateGroupRules (type, params) {
  if (type === 'add') {
    return request.post(`/security_group/${params.id}/rule`, params);
  } else if (type === 'update') {
    return request.put(`/security_group/rule/${params.id}`, params);
  }
}
export function deleteGroupRules (id) {
  return request.delete(`/security_group/rule/${id}`);
}
// 批量添加安全组规则
export function batchRules (params) {
  return request.post(`/security_group/${params.id}/rule/batch`, params);
}

// 安全组实例列表
export function getGroupCloud (params) {
  return request.get(`/security_group/${params.id}/host`, {params});
}
// 关联安全组
export function concatCloud (params) {
  return request.post(`/security_group/${params.id}/host/${params.host_id}`,params);
}
// 取消关联实例
export function cancelConcatCloud (params) {
  return request.delete(`/security_group/${params.id}/host/${params.host_id}`);
}
// 获取所有可用实例
export function getAllCloud () {
  return request.get(`/idcsmart_cloud/all`);
}