/* 实例 */
import request from "../utils/request";

// 获取数据中心
export function getDataCenter (id) {
  return request.get(`/product/${id}/idcsmart_cloud/data_center`);
}
// 获取宽带类型
export function getBwType (id) {
  return request.get(`/product/${id}/idcsmart_cloud/bw_type`);
}
// 计算能力
export function getPackage (params) {
  return request.get(`/product/${params.product_id}/idcsmart_cloud/package`, {
    params,
  });
}
// 操作系统
export function getSystem (params) {
  return request.get(
    `/product/${params.product_id}/idcsmart_cloud/system_image`,
    { params }
  );
}
// vpc
export function getVpc (params) {
  return request.get(`/vpc`, { params });
}
// 安全组
export function getGroup (params) {
  return request.get(`/security_group`, { params });
}
// 获取其他配置
export function getSetting (id) {
  return request.get(`/product/${id}/idcsmart_cloud/config`);
}
// 周期
export function getDurationPprice (params) {
  return request.get(`/idcsmart_cloud/duration_price`, { params });
}
// 修改配置计算价格package_id,image_id,backup_enable,panel_enable
export function updatePrice (params) {
  return request.post(`/product/${params.product_id}/config_option`, params);
}

// 结算商品
export function settleProduct (params) {
  return request.post(`/product/settle`, params);
}

// 使用优惠码
export function usePromoCode (params) {
  return request.post(`/promo_code/apply`, params);
}

// 支付{id: 订单id,gateway:支付方式}
export function onlinePay (params) {
  return request.post(`/pay`, params);
}
// 使用余额支付{id:订单id,use:1使用，0取消}
export function creditPay (params) {
  return request.post(`/credit`, params);
}
// 获取支付状态
export function getPayStatus (id) {
  return request.get(`/pay/${id}/status`);
}

// 订单详情
export function getOrderDetail (id) {
  return request.get(`/order/${id}`);
}
/* 实例列表 */
export function getCloudList (params) {
  return request.get(`/idcsmart_cloud`, { params });
}
// 支付接口
export function payWayList () {
  return request.get(`/gateway`);
}

// 账号余额
export function getUserDetail () {
  return request.get(`/account`);
}

// 实例详情
export function getCloudDetail (id) {
  return request.get(`/idcsmart_cloud/${id}`);
}
// ssh key
export function getSshkey (params) {
  return request.get(`/ssh_key`, { params });
}
// cloud日志
export function getLog (params) {
  return request.get(`/idcsmart_cloud/${params.id}/log`, { params });
}

// 获取产品下的磁盘
export function getDiskById (id) {
  return request.get(`/idcsmart_cloud/${id}/disk`)
}

// 获取磁盘列表详情
export function getDisk (params) {
  return request.get(`idcsmart_cloud_disk`, { params });
}

// 卸载磁盘
export function umount (id) {
  return request.put(`/idcsmart_cloud_disk/${id}/umount`);
}

// 删除磁盘
export function delDisk (id) {
  return request.delete(`/idcsmart_cloud_disk/${id}`);
}

// 挂载磁盘
export function mount (params) {
  return request.put(`idcsmart_cloud_disk/${params.id}/mount`, params);
}

// 扩容磁盘
export function expansion (params) {
  return request.put(`idcsmart_cloud_disk/${params.id}/expansion`, { params });
}

// 快照
// 快照列表
export function snapshot (params) {
  return request.get(`idcsmart_cloud/${params.id}/snapshot`, { params });
}
// 创建快照
export function createSnapshot (params) {
  return request.post(`idcsmart_cloud/${params.id}/snapshot`, params)
}
// 快照还原
export function restoreSnapshot (params) {
  return request.post(`idcsmart_cloud/${params.id}/snapshot/restore`, params)
}
// 删除快照
export function delSnapshot (params) {
  return request.delete(`idcsmart_cloud/${params.id}/snapshot/${params.snapshot_id}`)
}

// 备份列表
export function backup (params) {
  return request.get(`idcsmart_cloud/${params.id}/backup`, { params });
}

// 创建备份
export function createBackup (params) {
  return request.post(`idcsmart_cloud/${params.id}/backup`, params)
}
// 备份还原
export function restoreBackup (params) {
  return request.post(`idcsmart_cloud/${params.id}/backup/restore`, params)
}
// 删除备份
export function delBackup (params) {
  return request.delete(`idcsmart_cloud/${params.id}/backup/${params.backup_id}`)
}

// 产品详情
export function host (id) {
  return request.get(`host/${id}`)
}

// 停用页面信息
export function refund (params) {
  return request.get(`refund`, { params })
}

// 申请停用
export function postRefund (params) {
  return request.post(`refund`, params)
}

// 取消停用
export function quitRefund (params) {
  return request.put(`refund/${params.id}/cancel`)
}

// 续费页面信息
export function renew (params) {
  return request.get(`host/${params.id}/renew`, { params })
}

// 续费
export function doRenw (params) {
  return request.post(`host/${params.id}/renew`, params)
}

// 升降级配置
export function getupgradeOp (params) {
  return request.get(`/host/${params.id}/upgrade/config_option`, { params });
}

//获取图表数据
export function chartdata (params) {
  return request.get(`/idcsmart_cloud/${params.id}/chart`, { params });
}
//获取当前VPC网络
export function vpcdata (params) {
  return request.get(`/idcsmart_cloud/${params.id}/vpc`, { params });
}
// 切换vpc网络
export function changeVpc (params) {
  return request.put(`/idcsmart_cloud/${params.id}/vpc`, params);
}
//获取网络流量
export function flowdata (params) {
  return request.get(`/idcsmart_cloud/${params.id}/flow`, { params });
}

//  获取产品停用信息
export function refundMsg (id) {
  return request.get(`/refund/host/${id}/refund`)
}

// 获取升降级套餐价格
export function getPackagePrice (params) {
  return request.get(`/idcsmart_cloud/${params.id}/package/upgrade`, { params })
}
// 生成升降级套餐订单
export function createPackageId (params) {
  return request.post(`/idcsmart_cloud/${params.id}/package/upgrade/order`, params)
}

// 修改概述页面备注
export function changeNotes (params) {
  return request.put(`/host/${params.id}/notes`, params);
}